// src/ErrorPage.js

import React from "react";
import { useParams } from "react-router-dom";

export default function ErrorPage() {
  const { error } = useParams();
  console.error(error);

  return (
    <div id="error-page">
      <h1>Oops!</h1>
      <p>Sorry, an unexpected error has occurred.</p>
      <p>
        <i>{error?.statusText || error?.message}</i>
      </p>
    </div>
  );
}
